<template>
<view class="">
	<view class="" v-show="allAflag">
		<view class="bgc-o-r" style="height: 20rpx;"></view>
		<view class="bgc-o-r df" style="align-items: center; height: 210rpx;">
			<view class="b-r-50 df-jcc-aic p-vps" style="">
				<image :src="arr[currentValue.avatarId]" class="b-r-50" mode="widthFix" 
				style="width: 190rpx; background-color: #fff9d5; margin-top: 20rpx;"></image>
				<view class="" style="position: absolute; left: 142rpx; top: -20rpx;">
					<image src="../../static/mlestones/Crown.png" mode="widthFix" 
					style="width: 70rpx; "></image>
				</view>
			</view>
			<view class="">
				<view class="FFF fw800 f36rpx">{{currentValue.nickname}}</view>
				<view class="df f24rpx fw800">
					<view class="" style="color: #225877; padding-right: 10rpx; width: 130rpx;">Not you?</view>
					<view @click="switchProfiles"  style="" class="FFF"
					hover-class="hover" hover-start-time=0 hover-stay-time=150 
					>Switch profiles</view>
				</view>
			</view>
		</view>
		<view class="" style="width: 100vw; height: 92rpx;">
			<view class="bgc-o-y" style="height: 22rpx;"></view>
			<view class="df f26rpx fw800 ta-c bgc-o-y" style="color: #ea521f; line-height: 70rpx;">
				<view class="perBox bgc-o-y" style=""></view>
				<view @click="goOverview" class="perBox2" :style="iSstyle == 1 ? clpertrl : pertrl ">
					OVERVIEW
				</view> 
				<view @click="goActivity" class="perBox2" :style="iSstyle == 2 ? clpertrl : pertrl ">
					ACTIVITY
				</view>
				<view @click="gorewards" class="perBox2" :style="iSstyle == 3 ? clpertrl : pertrl ">
					REWARDS
				</view>
				<view class="perBox bgc-o-y" style=""></view>
			</view>
		</view>
		<!-- OVERVIEW开始 -->
		<view class="bgc_FFF" style="height: calc(100vh - 332rpx);" v-show="overviewFlag">
			<scroll-view scroll-y="true" style="height: calc(100vh - 332rpx);">
				<view class="" style="height: 30rpx;"></view>
				<!-- :getMonthData="getMonthData" -->
				<!-- 头部点击切换层 -->
				<view class="head df-jcc-aic" style="	position: relative;">
					<view class="" hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
						<image src="../../static/mlestones/zuo_FFF.png" mode="heightFix" 
							@click="switch_monthsub"
							style="height: 40rpx; position: absolute; top: 5rpx; left: 0rpx;"></image>
					</view>
					<view style="width: 500rpx;" class="ta-c FFF f24rpx fw800">{{Month[month]}} {{year}}</view>
					<view class="" hover-class="hover1" hover-start-time=0 hover-stay-time=150 > 
						<image src="../../static/mlestones/you_FFF.png" mode="heightFix" 
							@click="switch_monthPlus"
							style="height: 40rpx; position: absolute; top: 5rpx; right: 0rpx;"></image>
					</view>
				</view>
				<view class="" style="height: 17rpx"></view>
				<view class="b-b--rl-22" style="margin: 0rpx 62rpx 0;">
					<view class="df-jcc-aic" style="padding-bottom: 20rpx; width: 624rpx; border-top: 3rpx solid #ede7dc; border-left: 5rpx solid #ede7dc; border-right: 5rpx solid #ede7dc;">
						<view class="">
							<view class="" style="margin: 20rpx 0 0;">
								<view class="" > 
									<view class="f24rpx fw800 df" style="color: #ea521f; margin-bottom: 10rpx;">
										<view class="ta-c " style="width: 89rpx;" 
										v-for="(item,index) in week_O" :key="index">{{item}}</view>
									</view>
								</view>
							</view>
							<view class="df f22rpx thisDay" style="word-break: break-all; flex-wrap: wrap; ">
								<view class="ta-c df-jcc-aic" 
									v-for="(item2,index2) in thisMouth" :key="index2+'a'"
									style="width: 89rpx; height: 56rpx;  position: relative; " > 
									<view class="df-jcc-aic"
									style=""
									:style="item2.isW == 1 ? 'background-color: #fee4a1; border-radius: 10rpx;': ''  ">
										<view style="width: 52rpx; height: 46rpx; color: #0b4d6e; z-index: 2; line-height: 46rpx;"
										:style="item2.cass === 'no' ? 'color: #d0d0d0;':'color: #0b4d6e;' "
										:class="{
											tokey:getThisDay(item2),
										}"> 
											{{item2.day}} 
										</view>
									</view>
									<view class="" v-if="getJiangbei(item2.day,index2)" 
										style="z-index: 0; position: absolute; left: 40rpx; bottom: 0rpx;">
										<image src="../../static/mlestones/jbb.png" mode="widthFix" style="width: 60rpx; "></image>
									</view> 
								</view>
							</view> 
						</view>
					</view>
					
					<view class="b-b--rl-22" style="background-color: #1da89e; box-shadow:  0rpx 1rpx .5rpx #c4b28c;">
						<view class="f27rpx ta-c fw800 FFF" style="height: 85rpx;  line-height: 85rpx;">
							Activity Summary 
						</view>
						<view class="df-jcsb" style="margin: 0 10rpx;">
							<view class="df-jcsb FFF fw800 cale_box" style="">
								<view class="df-jcc-aic" v-if="MonthData.succeeded">
									<view class="df-jcc-aic" style="height: 100%;">
										<view class="cale_box_1 ta-c" style="line-height: 94rpx;">{{MonthData.data.activeDays}}</view>
									</view>
									<view class="f20rpx fw500" style="margin-left: 9rpx;">
										<view class="">Active</view> 
										<view class="">Days</view>
									</view>
								</view>
								<view class="df-jcc-aic" style="margin-right: 24rpx;">
									<image src="../../static/mlestones/Activeicon.png" 
									mode="widthFix" style="width: 101rpx;"></image>
								</view>
							</view>
							<view class="df-jcsa FFF fw800 cale_box" style="align-items: center;">
								<view class="df-jcc-aic" v-if="MonthData.succeeded">
									<view class="cale_box_1" style=";">{{MonthData.data.inactiveDays}}</view>
									<view class="f20rpx fw500" style="margin-left: 9rpx;">
										<view class="">Non Active</view> 
										<view class="">Days</view>
									</view>
								</view>
								<view class="df-jcc-aic" style="margin-right: 10rpx;">
									<image src="../../static/mlestones/Zzz.png" 
									mode="widthFix" style="width: 80rpx;"></image>
								</view>
							</view>
						</view>
						<view class="" style="height: 10rpx;"></view>
						<view class="df-jcsb" style="margin: 0 10rpx;">
							<view  class="df-jcsb FFF fw800 cale_box" style="" 
								v-for="(hx,ly) in allMountDate" :key='ly' >
								<view class="df-jcc-aic" >
									<view class="cale_box_1" style="">{{hx.activeDays}}</view>
									<view class="f20rpx fw500" style="margin-left: 9rpx;">
										<view class="">{{hx.code == 'UNIS_BC01'?'JR.BOUNCER':'SQUEEZIES'}}</view> 
										<view class="">Days</view>
									</view>
								</view>
								<view class="df-jcc-aic" style="margin-right: 24rpx;">
									<image :src="hx.code == 'UNIS_BC01'? img2:img1 " 
									mode="widthFix" style="width: 74rpx;"></image>
								</view> 
							</view>
							
							<view v-if="false" class="df-jcsb FFF fw800 cale_box" style="">
								<view class="df-jcc-aic" v-if="MonthData.succeeded">
									<view class="cale_box_1" style="">{{MonthData.data.datas[0].inactiveDays}}</view>
									<view class="f20rpx fw500" style="margin-left: 9rpx;">
										<view class="">Squeezies</view>  
										<view class="">Days</view>
									</view>
								</view>
								<view class="df-jcc-aic" style="margin-right: 24rpx;">
									<image src="../../static/mlestones/plushy.png" 
									mode="widthFix" style="width: 74rpx;"></image>
								</view>
							</view>
						</view>
						<view class="" style="height: 23rpx;"></view>
					</view>
				</view>
				
				<view class=" b-t-r-l-22 b-b-r-l-10"
					v-for="(items,indexs) in allMountDate" :key='indexs'
					style="margin: 20rpx 60rpx 0; height: 250rpx;border-right: 1rpx solid #ede7dc;; border-left: 1rpx solid #ede7dc;; border-bottom: 5rpx solid #ede7dc;">
					<view :style="items.code == 'UNIS_BC01'? bgcL:bgcZ " class="b-t-r-l-22 df" style="height: 150rpx; ">
						<view  class="b-t-r-l-22 df-jcc-aic" style="margin: 0 60rpx 0 31rpx;">
							<image :src="items.code == 'UNIS_BC01'? imgL:imgZ " mode="widthFix" style="width: 117rpx"></image>
						</view>
						<view class="fw800 FFF" style="font-size: 48rpx; line-height: 150rpx;">
						{{items.code == 'UNIS_BC01'?'JR.BOUNCER':'SQUEEZIES'}}</view>
					</view>
					<view class="df-jcsb ta-c f21rpx fw800 "
						:style=" items.code == 'UNIS_BC01'? 'margin: 0 60rpx 0':'margin: 0 84rpx 0 70rpx;' "
						style="align-items: center; height: 100rpx;"> 
						<view class="">
							<view class="color_t">Time</view> 
							<view class="color_b">{{items.time}} min</view>
						</view>
						<view class="" v-if="items.code == 'UNIS_BC01'? true:false ">
							<view class="color_t">Jumps</view>
							<view class="color_b">{{items.jumps}}</view>
						</view>
						<view class=""  v-if="items.code == 'UNIS_BC01'? true:false ">
							<view class="color_t">Distance</view>
							<view class="color_b">{{items.distance}} m</view> 
						</view>
						<view class="" v-if="items.code == 'UNIS_BC01'? false:true ">
							<view class="color_t">Squeezing Times</view>
							<view class="color_b">{{items.squeezetimes}}</view>
						</view>
						<view class="">
							<view class="color_t">GoPower</view>
							<view class="color_b">{{items.goPower}} points</view>
						</view>
					</view>
				</view>
				<view class="" style="height: 50rpx;"></view>
				<view class="" style="height: 30rpx;"></view>
 			</scroll-view> 
		</view>
		<!-- ACTIVITY开始 -->
		<view class="bgc_FFF" v-show="activityFlag" style="height: calc(100vh - 332rpx);">
			<scroll-view scroll-y="true" style="height: calc(100vh - 332rpx);">
			<view class="" style="margin: 0 27rpx; ">
				<view class="" style="height: 37rpx;"></view>
				<view class="" style="">
					<!-- 导入日历组件 DayData -->
					<view class=" df" style="flex-direction: column; margin: 0 63rpx; position: relative;">
						<view class="" style="">
							<view class="" @click="weekPre" style="position: absolute; top: 15rpx; left: -40rpx;">
								<view class="" hover-class="hover1" hover-start-time=0 hover-stay-time=150>
									<image  src="../../static/mlestones/zuo.png" mode="widthFix" style="width: 40rpx;"></image>
								</view>
							</view>
							<view class="" @click="weekNext" style="position: absolute; top: 15rpx; right: -40rpx;"
							hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
								<image  src="../../static/mlestones/you.png" mode="widthFix" style="width: 40rpx;"></image>
							</view>
							<view class="df-jcsb">
								<view class="ta-c fw800" 
								style="font-size: 15rpx; color: #225877; width: 60rpx;" 
								:style="index1 == 0 ? 'color: #ea521f;':'color: #40aebe;' "
								
								v-for="(item1,index1) in week" :key="index1">
								{{item1}}
								</view>
							</view>
							<view class="df-jcsb"> 
								<view ref="pick" class="ta-c f30rpx col" @click="pick(item,days,index)" 
								:style="statly == index ? 'color: #40aebe; border: 2rpx solid #225877;border-radius: 10rpx;':'color: #225877;'"
								style="width: 56rpx; height: 54rpx; line-height: 54rpx; margin-top: 15rpx; " 
								v-for="(item,index) in days" :key="index">
									{{item.getDate()}}
								</view>
							</view> 
						</view>
						<view class="" style="height: 15rpx;"></view>
					</view>
					<!-- <calendarSmo ></calendarSmo> -->
				</view> 
				<view class="df" style="" >
					<view class="f24rpx fw800" style="color: #2fa8a0; margin: 28rpx 45rpx 28rpx 54rpx;">
						<view class="">Daily</view> 
						<view class="">Activity</view>
						<view class="">Summary</view>  
					</view>
					<view class="" style="position: relative;">
						<image src="../../static/mlestones/Ellipse.png" mode="heightFix" 
						style="height: 136rpx;"></image> 
						<view class="f24rpx fw800  b-r-50"  
							style=" position: absolute; top: 27rpx; left: 84rpx; color: #2b94cf;">
							<view class="ta-c b-r-50" style="width: 57rpx; height: 57rpx; line-height: 57rpx;">   
								{{dayWTime}}
							</view>
							</view>  
					</view>
					<view class="" style="margin-left: 24rpx; position: relative;">
						<image src="../../static/mlestones/Ellipse2.png" mode="heightFix" 
						style="height: 136rpx;"></image>
						<view style="position: absolute; top: 24rpx; left: 102rpx; color: #ea521f; font-size: 20rpx;" 
							class=" df-jcc-aic">
							<view class="b-r-50 ta-c" style="width: 57rpx; height: 57rpx; line-height: 57rpx;">
								{{dayWGoPower}}
							</view>
						</view>
					</view>
				</view>
				<view class=" b-t-r-l-22 b-b-r-l-10" 
					v-for="(it,ind) in allDayDate" :key='ind'
					style="margin-top: 24rpx; height: 250rpx;border-right: 1rpx solid #ede7dc;; border-left: 1rpx solid #ede7dc;; border-bottom: 5rpx solid #ede7dc;">
					<view :style="it.code == 'UNIS_BC01'? bgcL:bgcZ " class="b-t-r-l-22 df" style="height: 150rpx; ">
						<view  class="b-t-r-l-22 df-jcc-aic" style="margin: 0 60rpx 0 31rpx;">
							<image :src="it.code == 'UNIS_BC01'? imgL:imgZ " mode="widthFix" style="width: 117rpx"></image>
						</view>
						<view class="fw800 FFF" style="font-size: 48rpx; line-height: 150rpx;">{{it.code == 'UNIS_BC01'?'JR.BOUNCER':'SQUEEZIES'}}</view>
					</view>
					<view class="df-jcsb ta-c f21rpx fw800 "
						:style=" it.code == 'UNIS_BC01'? 'margin: 0 60rpx 0':'margin: 0 84rpx 0 70rpx;' "
						style="align-items: center; height: 100rpx;"> 
						<view class="">
							<view class="color_t">Time</view> 
							<view class="color_b">{{it.time}} min</view>
						</view>
						<view class="" v-if="it.code == 'UNIS_BC01'? true:false ">
							<view class="color_t">Jumps</view>
							<view class="color_b">{{it.jumps}}</view>
						</view>
						<view class=""  v-if="it.code == 'UNIS_BC01'? true:false ">
							<view class="color_t">Distance</view>
							<view class="color_b">{{it.distance}} m</view> 
						</view>
						<view class="" v-if="it.code == 'UNIS_BC01'? false:true ">
							<view class="color_t">Squeezing Times</view>
							<view class="color_b">{{it.squeezetimes}}</view>
						</view>
						<view class="">
							<view class="color_t">GoPower</view>
							<view class="color_b">{{it.goPower}} points</view>
						</view>
					</view>
				</view>
			</view>
			<view class="" style="height: 70rpx;"></view>
			</scroll-view>
		</view>
		<!-- REWARDS开始 -->
		<view class="bgc_FFF" v-show="rewardsFlag" style="height: calc(100vh - 332rpx);"> 
			<scroll-view scroll-y="true" style="height: calc(100vh - 332rpx);">
			<view class="" style="height: 45rpx;"></view>
			<view class="df " style="">
				<view class="box-sha b-t-r-l-22" style="margin: 0 27rpx 0; width: 250rpx; height: 520rpx;">
					<view class="f36rpx FFF fw800 ta-c b-t-r-l-22" 
					style="background-color: #1da89e;  height: 57rpx; line-height: 57rpx; ">GoPower</view>
					<view class="ta-c fw800" 
					style="margin: 30rpx 0 30rpx 0; font-size: 60rpx; color: #ffc107; ">
						{{allGoPower}}
					</view>
					<view class="df-jcc-aic">
						<image src="../../static/mlestones/map_render.png" 
							style="border-radius: 10rpx; width: 187rpx; height: 235rpx;"></image>
					</view>
					<view class="box-sha f21rpx fw800 ta-c" @click="goGoMapProgress"
					hover-class="hover" hover-start-time=0 hover-stay-time=150 
					style="margin: 20rpx 16rpx 0; height: 52rpx; border-radius: 22rpx; line-height: 52rpx; color: #225877;"> 
						GoMap Progress
					</view>
					<view class="" style="height: 17rpx;"></view>
				</view>
				<view class="box-sha b-t-r-l-22" style="width: 418rpx; height: 520rpx;">
					<view class="f36rpx FFF fw800 ta-c b-t-r-l-22"
						style="background-color: #1da89e;  height: 57rpx; line-height: 57rpx; ">Trophies</view>
					<view class="" style="margin: 50rpx 23rpx 0; height: 370rpx;">
						<swiper :circular="true"  :autoplay="true" :interval="5000" :duration="1000" style="height: 370rpx;">
							<swiper-item  style="position: relative;">
								<view class="df-jcc-aic" >
									<image src="../../static/mlestones/yinBei.png" mode="" style="width: 370rpx; height: 360rpx;"></image>
								</view> 
								<view class="df-jcc-aic" style="position: absolute; left: 145rpx; bottom: 0;">
									<view class="f30rpx fw800 ta-c" style="width: 100rpx; color: #c67e55;">
										x {{tongBei}}
									</view>  
								</view> 
							</swiper-item>  
							<swiper-item v-if="yinBei>0 ? true : false" style="position: relative;">
								<view class="df-jcc-aic" >
									<image src="../../static/mlestones/tongBei.png" mode="" style="width: 370rpx; height: 360rpx;"></image>
								</view>
								<view class="df-jcc-aic" style="position: absolute; left: 145rpx; bottom: 0;">
									<view class="f30rpx fw800 ta-c" style="width: 100rpx; color: #a8a8a8;">
										x{{yinBei}}
									</view>
								</view>
							</swiper-item> 
							<swiper-item v-if="jinBei>0 ? true : false" style="position: relative;"> 
								<view class="df-jcc-aic" >
									<image src="../../static/mlestones/jinbei.png" mode="" style="width: 370rpx; height: 360rpx;"></image>
								</view>
								<view class="df-jcc-aic" style="position: absolute; left: 145rpx; bottom: 0;">
									<view class="f30rpx fw800 ta-c" style="width: 100rpx; color: #fecb4d;">
										x{{jinBei}}
									</view>
								</view>
							</swiper-item>
						</swiper>	
					</view>
				</view>
			</view> 
			<view class="box-sha b-t-r-l-22" style="margin: 27rpx 27rpx 0;">
				<view class="f36rpx FFF fw800 ta-c b-t-r-l-22"
				style="background-color: #1da89e;  height: 57rpx; line-height: 57rpx; ">Badges</view>
				<view class="df-jcsa" style="margin: 0 0rpx; height: 160rpx;" >
					<view v-if="maoBadge>0 ? true : false" style="height: 100%; margin: 30rpx 0 0 0;" >
						<view class="df-jcsb" style="flex-direction: column; align-items: center; height: 100%;">
							<view class="df-jcc-aic" style="height: 115rpx;">
								<image src="../../static/mlestones/hz/Adventure.png" mode="widthFix" 
								style="width: 114rpx; height: 113rpx;"></image>
							</view>
							<view class="ta-c" style="font-size: 21rpx; color: #6a2875;">Adventure x {{maoBadge}}</view>
						</view>
					</view>
					<view v-if="tangBadge>0 ? true : false" class="" style="height: 100%; margin: 30rpx 0 0 0;" >
						<view class="df-jcsb" style="flex-direction: column; align-items: center; height: 100%;">
							<view class="df-jcc-aic" style="height: 115rpx;">
								<image src="../../static/mlestones/hz/Super_Star.png" mode="widthFix" 
								style="width: 114rpx; height: 113rpx;"></image>
							</view>
							<view class="ta-c" style="font-size: 21rpx; color: #ea521f;">Super Star x {{tangBadge}}</view>
						</view>
					</view>
					<view v-if="juBadge>0 ? true : false" class="" style="height: 100%; margin: 30rpx 0 0 0;" >
						<view class="df-jcsb" style="flex-direction: column; align-items: center; height: 100%;"> 
							<view class="df-jcc-aic" style="height: 115rpx;">
								<image src="../../static/mlestones/hz/Explorer.png" mode="widthFix"
								style="width: 114rpx; height: 113rpx;"></image>
							</view>
							<view class="ta-c" style="font-size: 21rpx; color: #378944;">Explorer x {{juBadge}}</view>
						</view> 
					</view>
					<view v-if="newbieBadge>0 ? true : false" class="" style="height: 100%; margin: 30rpx 0 0 0;" >
						<view class="df-jcsb" style="flex-direction: column; align-items: center; height: 100%;">
							<view class="df-jcc-aic" style="height: 115rpx;">
								<image src="../../static/mlestones/hz/Rookie.png" mode="widthFix" 
								style="width: 114rpx; height: 113rpx;"></image>
							</view>
							<view class="ta-c" style="font-size: 21rpx; color: #225877;">Rookie x {{newbieBadge}}</view>
						</view>  
					</view>
				</view>
				<view class="" style="height: 30rpx; margin-top: 23rpx;"></view> 
			</view> 
