				this.getTrophy(newol.id)
				this.getDitu(newol.id)
				this.initData()
				this.statly = new Date().getDay()-1;
				if(this.statly == -1){
					this.statly == 6
				} 
			}  
			
		},
		data(){
			return{
				iSstyle:1,
				overviewFlag:true, 
				activityFlag:false,
				rewardsFlag:false,
				allAflag:true,
				allAsflag:false,
				hasCopperTrophy:false,
