				hasSilverTrophy:false,
				pupFlag:false,
				dengFlag:false,
				toGoldTrophy:null,
				clpertrl:`background-color: #FFF;
 									border-top-right-radius: 10rpx;
									border-top-left-radius: 10rpx;
									border-top: 1rpx solid #baa87b;
									border-left: 1rpx solid #baa87b; 
									border-right: 1rpx solid #baa87b;`,
				pertrl:`	background-color: #fecc4e;
									border-bottom: 1rpx solid #baa87b;`,
				getMonthData:'',
				arr:["../../static/mlestones/left1.png","../../static/mlestones/left2.png","../../static/mlestones/left3.png","../../static/mlestones/right4.png","../../static/mlestones/right5.png","../../static/mlestones/right6.png"],
				guanKaUrl:'../../static/milestones/01.jpg',
				week_O:['M','T','W','T','F','S','S'],
				Month:['','JANUARY','FEBRUARY','MARCH','APRIL','MAY','JUNE','JULY','AUGUST','SEPTEMBER','OCTOBER','NOVEMBER','DECEMBER'],
				week:['MON','TUE','WEO','THU','FRI','SAT','SUN'],
				bgcL:'background-color: #2b94cf;',
				bgcZ:'background-color: #955798;',
				imgL:'../../static/img/Trampoline.png',
				imgZ:'../../static/img/squeeze.png',
				img1:'../../static/mlestones/plushy.png',
				img2:'../../static/mlestones/3.png',
				year:new Date().getFullYear(),
				month:new Date().getMonth() + 1,
				getData:{},
				DayData:{},
				MonthData:{}, 
				thisMouth:{},
				// dayBCData:[],
				// dayJSQData:[],
				mBCData:[],
				mJSQData:[],
				lastMonth:[],
				nextMouth:[],
				days: [],
				wGameData:[],
				allDayDate:[],
				allMountDate:[],
				dayWTime:0,
				dayWGoPower:0,
				endDayss:0,
				currentYear: 1970,   // 年份
				currentMonth: 1,  // 月份
				currentDay: 1,    // 日期
				currentWeek: 1,    // 星期
				weekstart:0 ,
				jinBei:0,
				yinBei:0,
				tongBei:0,
				maoBadge:0,
				tangBadge:0,
				newbieBadge:0,
				juBadge:0,
				allGoPower:0,
				statly:0,
				baiFenBi:0,

			}
		},
		
		methods:{
			backTong(){
				this.pupFlag = false
				this.allAsflag = false
				this.allAflag = true
			},
			backYing(){
				this.hasSilverTrophy = false
			},
			backDeng(){
				this.dengFlag = false
			},
			backJing(){
				this.hasGoldTrophy = false
			},
			async getDitu(ida){
				const dit = await this.$myRequest({
					url:'/api/game/data/checkpoint' + '?playerId=' + ida
				})
				this.guanKaUrl = dit.data.data.imageUrl
				this.baiFenBi = dit.data.data.progress
			},
			goGoMapProgress(){
				this.allAflag = false
				this.allAsflag = true
			},
			backallAflag(){
				this.allAsflag = false
				this.allAflag = true
			},
			async getTrophy(id){
				let _this = this
				let year_s = new Date().getFullYear()
				let month_s = new Date().getMonth() + 1
				let day_s = new Date().getDate()
				
				let times = year_s+''+month_s+''+day_s
				// console.log(times);
				const tro = await this.$myRequest({
					url:'/api/game/data/rewards' + '?playerId=' + id
				})
				// console.log('tro');
				// console.log(tro);
				
				if(tro.data.succeeded){
					this.jinBei = tro.data.data.goldTrophy
					this.yinBei = tro.data.data.silverTrophy
					this.tongBei = tro.data.data.copperTrophy
					this.maoBadge = tro.data.data.adventureBadge
					this.tangBadge = tro.data.data.explorerBadge
					this.newbieBadge = tro.data.data.newbieBadge
					this.juBadge = tro.data.data.superStarBadge
					this.allGoPower = tro.data.data.goPower
					this.hasCopperTrophy = tro.data.data.hasCopperTrophy
					this.hasGoldTrophy = tro.data.data.hasGoldTrophy
					this.hasSilverTrophy = tro.data.data.hasSilverTrophy
					this.toGoldTrophy = tro.data.data.toGoldTrophy
					this.ides = tro.data.data.playerId
				}
				if(this.toGoldTrophy == null){
					this.dengFlag = false
				}else{
					this.dengFlag = true
				}
				// 获取奖杯信息
				if(!this.hasCopperTrophy && !this.hasGoldTrophy && !this.hasSilverTrophy && (this.toGoldTrophy == null)){
					// console.log('一个奖杯都没');
				}else{
					// console.log('有奖杯');
					let ids = tro.data.data.playerId
					let dataFlag = uni.getStorageSync(ids)
					// console.log('缓存数据'+dataFlag);
					if(dataFlag){
						// console.log('不为空');
						// console.log('缓存时间'+dataFlag);
						// console.log('当前时间'+times);
						if(times-dataFlag<1){
							// console.log('当天');
							// uni.setStorageSync(ids,times)

						}else{
							uni.removeStorageSync(ids)
							// console.log('显示1');
							// uni.setStorageSync(ids,times)
							// this.allAflag = false
							// this.pupFlag = true
						}
					}else{
						// console.log('为空');
						uni.setStorageSync(ids,times)
						this.allAflag = false
						this.pupFlag = true
					}
					// let Flag = uni.getStorageSync(ids)
					// let a = _this.getFlag(ids)
					// console.log(a);
					// let objk = {
					// 	id:ids,
					// 	time:times,
					// 	flag:0
					// }
					// console.log(objk);
					// uni.setStorageSync('obj',objk)
					// if(a == 0){
					// 	uni.setStorageSync(ids,1)

					// }
				} 
			},
			getFlag(ids){
				let flag = uni.getStorageSync('obj')
				if(flag.flag != 1){
					// console.log('不为1');
					return 0
				}else if(flag.flag == 1){
					// console.log('为1');
					return 1
				}
			}, 
			changeTime(){
				let _this = this
				let a = 202110271628
				let y = new Date().getFullYear()
				let m = new Date().getMonth()+1
				let d = new Date().getDate()
				let hours = new Date().getHours()
				let min = new Date().getMinutes()
				let time = y+''+m+''+d+''+hours+''+min
				console.log(time);
				if(time>=a){
					console.log('打开');
					uni.setStorageSync(_this.ides,0)
				}
				
			}, 
			formatDate (year, month, day) {
				const y = year
				let m = month
				if (m < 10) m = `0${m}`
				let d = day
				if (d < 10) d = `0${d}`
				return `${y}-${m}-${d}`
			}, 
			initData (cur) {
				let date = ''
				if (cur) {
					date = new Date(cur)
				} else {
					date = new Date()
				}
				this.currentDay = date.getDate()          // 今日日期 几号
				this.currentYear = date.getFullYear()       // 当前年份
				this.currentMonth = date.getMonth() + 1    // 当前月份
				this.currentWeek = date.getDay() // 1...6,0   // 星期几
				if (this.currentWeek === 0) {
					this.currentWeek = 7
				}
				const str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay)
				// 今日日期 年-月-日
				this.days.length = 0
				// 今天是周日，放在第一行第7个位置，前面6个 这里默认显示一周，如果需要显示一个月，则第二个循环为 i<= 35- this.currentWeek
				/* eslint-disabled */
				for (let i = this.currentWeek - 1; i >= 0; i -= 1) {
					const d = new Date(str)
					d.setDate(d.getDate() - i)
						// console.log(y:" + d.getDate())
					this.days.push(d)
				}
				for (let i = 1; i <= 7 - this.currentWeek; i += 1) {
					const d = new Date(str)
					d.setDate(d.getDate() + i)
					this.days.push(d)
				}
			}, 	
			//  上个星期
			weekPre () {
				const d = this.days[0]    // 如果当期日期是7号或者小于7号
				d.setDate(d.getDate() - 7)
				this.initData(d)
			},
			 
			//  下个星期
			weekNext () {
				const d = this.days[6]    // 如果当期日期是7号或者小于7号
				d.setDate(d.getDate() + 7)
				this.initData(d)
			},
			
			// 上一個月   传入当前年份和月份
			pickPre (year, month) {
				const d = new Date(this.formatDate(year, month, 1))
				d.setDate(0)
				this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
			},
			
			
			// 下一個月   传入当前年份和月份
			pickNext (year, month) {
				const d = new Date(this.formatDate(year, month, 1))
				d.setDate(35)
				this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
			},
			
			// 当前选择日期
			pick (date,days,index) {
				let ss = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
				this.getDayData(ss)
				this.statly = index
			},
			
			plcks(item){
				let e = item
				if(e == item){
					return false
				}else{
					return true
				}
			},
			
			//获取当天游戏数据
			async getDayData(day){
				const res2 = await this.$myRequest({
					url:'/api/game/data/activity' + '?playerId=' + this.id + '&time='+ day 
				})
				// console.log('');
				// console.log(res2.data.succeeded);
				let arr = []
				let _this = this
				// console.log(res2.data.data);
				if(res2.data.succeeded){
					_this.allDayDate = res2.data.data
					// console.log(_this.allDayDate);
					arr = res2.data.data
					// console.log('当天游戏数据');
					// console.log(arr);
					for(let i=0; i<arr.length; i++){
						_this.dayWTime = _this.dayWTime + arr[i].time
						_this.dayWGoPower = _this.dayWGoPower + arr[i].goPower
					}
					console.log('总时间');
					console.log(_this.dayWTime);
					console.log(_this.dayWGoPower);
				}
			},
			//当天数据显示
			getThisDaysss(){
				let year = new Date().getFullYear()
				let month = new Date().getMonth() +1
				let day = new Date().getDate()
				if(month<10){
					month = '0' + month
				}
				if(day<10){
					day = '0' + day
				}
				let days = year + '-' + month + '-' + day
				this.getDayData(days)
			},
			
			switchProfiles(){
				this.$emit('personalBack')
			},
			goOverview(){
				this.iSstyle = 1
				this.overviewFlag = true
				this.activityFlag = false
				this.rewardsFlag = false
				this.getMonthDatass(this.year,this.month)
			},
			goActivity(){
				this.iSstyle = 2
				this.activityFlag = true
				this.overviewFlag = false
				this.rewardsFlag = false
				this.getThisDaysss()
			},
			gorewards(){
				this.iSstyle = 3
				this.rewardsFlag = true
				this.activityFlag = false
				this.overviewFlag = false
			},
			
			//获取周数据
			getWeekDate(y,month){
				let m = Number(month)
				let lastDayMonth = new Date(y, m, 0).getDate() //当月最后一天
				let lastDayLastMonth = new Date(y, m-1, 0).getDate() //上个月最后一天
				
				let str = new Date().getDay();
			},
			
			//获取月份数据
			monthDay(y,month){
				let _this = this
				let dates = []
				let m = Number(month)
				// console.log('年：'+ y +'-月：'+m-1);
				let firstDayofMouth = new Date(y, m-1, 1).getDay() //当前第一天是周几
				// console.log('当月第一天是周'+firstDayofMouth);
				let lastDayofMonth = new Date(y, m, 0).getDate() //当月最后一天clog
				// console.log('当月最后一天：'+lastDayofMonth);
				let lastDayofLastMonth = new Date(y, m-1, 0).getDate() //上个月最后一天
				let startDay = (() => {
						if (firstDayofMouth > 0 && firstDayofMouth < 7) {
							return firstDayofMouth-1;
						}else if(firstDayofMouth == 0){
							return 6;
						}
				})(); 
				// console.log("s"+startDay);
				this.endDayss = startDay
				let endDay = (() => {
						return 42 - lastDayofMonth - startDay 
				})();
				// console.log('下个月剩余'+endDay);
				//上个月剩余的天数数组
				let lastRemainingDays = []
				for(let i=lastDayofLastMonth,j=0; i>(lastDayofLastMonth - startDay);i--,j++){
					lastRemainingDays.push({
						day:i,
						cass:'no',
						today:_this.year +'-'+ (month-1) +'-'+ i,
						isW:0
					})
				}
				lastRemainingDays = lastRemainingDays.reverse()
				this.lastMonth = lastRemainingDays.filter(n=>n)
				
				let daysNextMonth =  []
				for(var h=1; h<= endDay; h++){
					daysNextMonth.push({
						day:h,
						cass:'no',
						today:_this.year +'-'+ (month+1) +'-'+ h,
						isW:0
					})
				}
				// console.log('');
				this.nextMouth = daysNextMonth.filter(n=>n)
				let thisDaysMonth =  []

				for(var k=1; k<= lastDayofMonth; k++){
					thisDaysMonth.push({
						day:k,
						month:month,
						year:_this.year,
						cass:'ok',
						today:_this.year +'-'+month+'-'+k,
						isW:0
					})
				}
				let Mounthh = []
				Mounthh = Mounthh.concat(this.lastMonth).concat(thisDaysMonth).concat(this.nextMouth)
				this.thisMouth = Mounthh
			},
			
			//获取当月游戏数据
			async getMonthDatass(year, month){
				if(month<10){
					month = '0'+month
				}
				const res1 = await this.$myRequest({
					url:'/api/game/data/overview' + '?playerId=' + this.id + '&time='+ year +'-' + month +'-01'
				})
				this.wGameData = res1.data.data.dates
				this.MonthData = res1.data
				let _this = this
				// console.log(res1.data.data.datas);
				if(res1.data.succeeded){
					_this.allMountDate = res1.data.data.datas
				}
				
				let assa = this.thisMouth
				let bssb = this.wGameData
				for(var a = 0; a<assa.length;a++){
					for(var b=0; b<bssb.length;b++){
						if(assa[a].today == bssb[b]){
							assa[a].isW = 1
						}
					}
				} 
			},
			
			clickInit(){
				let assa = this.thisMouth
				for(var a = 0; a<assa.length;a++){
					assa[a].isW = 0 
				} 
			},
			
			switch_monthsub(){
				let _this = this
				this.month -= 1
				if(this.month < 1){
					this.year -= 1
					this.month = 12
					this.monthDay(_this.year,_this.month)
				}
				this.monthDay(_this.year,_this.month)
				this.getMonthDatass(_this.year,_this.month)
			},
			switch_monthPlus(){
				let _this = this 
				this.month += 1
				if(this.month > 12){
					_this.year += 1 
					_this.month = 1 
					this.monthDay(_this.year,_this.month)
				}
				this.monthDay(_this.year,_this.month)
				this.getMonthDatass(_this.year,_this.month)
			
			},
			
			//选中当前日期
			getThisDay(e){
				let y = new Date().getFullYear()
				let m = new Date().getMonth()+1
				let d = new Date().getDate()
				let date = y+'-'+m+'-'+d
				if(e.today == date){
					return true
				}else{
					return false
				}
			},
			//奖杯显示的位置
			getJiangbei(e,i){
				let s = i - this.endDayss 
				let date = new Date().getDate()+7
				if(s == date){
					return true
				}else{
					return false
				}
			},

		},
		mounted() {
			this.statly = new Date().getDay()-1;
			if(this.statly == -1){
				this.statly == 6
			} 
			this.monthDay(this.year,this.month)
			this.getWeekDate(this.year,this.month)
			// this.getThisDay()
			this.getJiangbei()
			this.getMonthDatass(this.year,this.month)
		},
		created() {
			this.initData(null)
			// this.getDayData('2021-10-06')
			this.getThisDaysss()
		}
	}
</script>

